import React from 'react';
import myPhoto from '../../_assets/images/myPhoto.jpg'
import styles from './home.module.scss';
import { Typography } from '@material-ui/core';

function Home() {
    return <div className={styles.container}>
        <div className={styles.info}>
            <div className={styles.img}>
                <img src={myPhoto} alt="Esmaeil Mazahery"></img>
            </div>
            <div className={styles.name}>
                <Typography variant="h3">Esmaeil Mazahery</Typography>
                <Typography variant="h4">Programmer</Typography>
                <Typography variant="body1">I'm a Software Developer . If you are into Web Design, Technology, Asp.net, C#, React, Angular, JavaScript, or Programming in general, check out stuff I made.</Typography>
            </div>
        </div>
        <div className={styles.contact}>
        <Typography variant="body1">Contact : +989179338319</Typography>
        <Typography variant="body1">Email : smaeil.mazahery@gmail.com</Typography>
        </div>
    </div>;
}

export default Home;