import { createMuiTheme } from '@material-ui/core/styles';

const theme = createMuiTheme({
    typography: {
        fontFamily: [
            '"Segoe UI"',
            '-apple-system',
            'BlinkMacSystemFont',
            'Roboto',
            '"Helvetica Neue"',
            'Arial',
            'sans-serif',
            '"Apple Color Emoji"',
            '"Segoe UI Emoji"',
            '"Segoe UI Symbol"',
        ].join(','),
        h3: {
            fontSize: '2.4rem',

        },
    },
    palette: {
        primary: {
            // light: will be calculated from palette.primary.main,
            main: '#4b4b4b',
            // dark: will be calculated from palette.primary.main,
            // contrastText: will be calculated to contrast with palette.primary.main
        },
    },
});

theme.typography.h3 = {
    ...theme.typography.h3,
    fontSize: '3rem',
    [theme.breakpoints.down('md')]: {
        fontSize: '2.4rem',
    },
    [theme.breakpoints.down('sm')]: {
        fontSize: '2rem',
    },
    
};

theme.typography.h4 = {
    ...theme.typography.h4,
    fontSize: '2.125rem',
    [theme.breakpoints.down('md')]: {
        fontSize: '1.9rem',
    },
    [theme.breakpoints.down('sm')]: {
        fontSize: '1.7rem',
    },
};

theme.typography.body1 = {
    ...theme.typography.body1,
    fontSize: '1rem',
    [theme.breakpoints.down('sm')]: {
        fontSize: '.9rem',
    },
};

export default theme