import React, { PureComponent } from 'react';
import {
    Switch,
    Route,
} from "react-router-dom";
import { Link } from 'react-router-dom';
import styles from './cvMenu.module.scss';
import { withStyles } from '@material-ui/core';
import { connectTo } from '../../../_utils/generic';

class CVMenu extends PureComponent {

    render() {
        const { location } = this.props

        return (
            <ul className={styles.list}>
                <li className={location.pathname === "/cv" ? styles.active : ""}><Link to="/cv">About</Link></li>
                {/* <li className={location.pathname === "/cv/experience" ? styles.active : ""}><Link to="/cv/experience">Experience</Link></li> */}
                <li className={location.pathname === "/cv/skills" ? styles.active : ""}><Link to="/cv/skills">Skills</Link></li>
            </ul>
        );
    }
}

export default withStyles(styles)(connectTo(
    state => ({
        location: state.router.location,
    }),
    {

    }, CVMenu))
