import React from 'react';
import styles from './styles.module.scss';
import Slider from "react-slick";
import afx1 from "../../_assets/images/works/afx/1.png"
import afx2 from "../../_assets/images/works/afx/2.png"
import afx3 from "../../_assets/images/works/afx/3.png"

import aspa11 from "../../_assets/images/works/aspa1/1.png"
import aspa12 from "../../_assets/images/works/aspa1/2.png"
import aspa13 from "../../_assets/images/works/aspa1/3.png"

import aspa21 from "../../_assets/images/works/aspa2/1.png"
import aspa22 from "../../_assets/images/works/aspa2/2.png"
import aspa23 from "../../_assets/images/works/aspa2/3.png"
import aspa24 from "../../_assets/images/works/aspa2/4.png"

import clinic1 from "../../_assets/images/works/clinic/1.jpg"
import clinic2 from "../../_assets/images/works/clinic/2.jpg"
import clinic3 from "../../_assets/images/works/clinic/3.jpg"
import clinic4 from "../../_assets/images/works/clinic/4.jpg"
import clinic5 from "../../_assets/images/works/clinic/5.jpg"
import clinic6 from "../../_assets/images/works/clinic/6.jpg"

import eskimo1 from "../../_assets/images/works/eskimo/1.jpg"
import eskimo2 from "../../_assets/images/works/eskimo/2.png"
import eskimo3 from "../../_assets/images/works/eskimo/3.png"

import hotwifi1 from "../../_assets/images/works/hotwifi/1.jpg"
import hotwifi2 from "../../_assets/images/works/hotwifi/2.png"
import hotwifi3 from "../../_assets/images/works/hotwifi/3.png"
import hotwifi4 from "../../_assets/images/works/hotwifi/4.png"

import kiosk1 from "../../_assets/images/works/kiosk/1.jpg"
import kiosk2 from "../../_assets/images/works/kiosk/2.png"
import kiosk3 from "../../_assets/images/works/kiosk/3.png"

import wp1 from "../../_assets/images/works/wp/1.png"
import wp2 from "../../_assets/images/works/wp/2.png"
import wp3 from "../../_assets/images/works/wp/3.png"
import Lightbox from 'react-image-lightbox';

function Work(props) {
    const { images, title, skills, imgHeight } = props;
    const [isOpen, setOpen] = React.useState(false);
    const [photoIndex, setPhotoIndex] = React.useState(0);
    const settings = {
        dots: false,
        infinite: true,
        speed: 300,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: false,
        arrows: false,
    }

    return <div className={styles.item}>
        <div className={styles.slider + " noFlex"}>
            <Slider {...settings} >
                {
                    images.map((item, index) => (
                        <div className={styles.sliderItem} key={index}>
                            <img className={styles.img + " " + imgHeight} src={item} alt="" onClick={() => { setOpen(true) }}></img>
                        </div>
                    ))
                }
            </Slider>
        </div>
        <div className={styles.info}>
            <p>{title}</p>
            <span>{skills}</span>
        </div>
        {isOpen && (
            <Lightbox
                mainSrc={images[photoIndex]}
                nextSrc={images[(photoIndex + 1) % images.length]}
                prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                onCloseRequest={() => setOpen(false)}
                onMovePrevRequest={() =>
                    setPhotoIndex((photoIndex + images.length - 1) % images.length)
                }
                onMoveNextRequest={() =>
                    setPhotoIndex((photoIndex + 1) % images.length)
                }
            />
        )}
    </div>
}


function MyWorks() {
    return <div className={styles.container}>
        <div className={styles.column}>
            <Work imgHeight={styles.img488} title="Kiosk" skills="ReactJs" images={[kiosk1, kiosk2, kiosk3]} ></Work>
            <Work imgHeight={styles.img100} title="Hotwifi" skills="Asp.net Mvc" images={[hotwifi1, hotwifi2, hotwifi3, hotwifi4]} ></Work>
            <Work imgHeight={styles.img150} title="AFX24.com" skills="Asp.net Mvc, Wordpress" images={[afx1, afx2, afx3]} ></Work>
        </div>
        <div className={styles.column}>
            <Work imgHeight={styles.img200} title="Clinic" skills="Wpf,Asp.net WebApi" images={[clinic1, clinic2, clinic3, clinic4, clinic5, clinic6]} ></Work>
            <Work imgHeight={styles.img275} title="ASPA" skills="Asp.net webform" images={[aspa11, aspa12, aspa13]} ></Work>
            <Work imgHeight={styles.img150} title="ASPA" skills="Asp.net Mvc" images={[aspa21, aspa22, aspa23, aspa24]} ></Work>
        </div>
        <div className={styles.column}>
            <Work imgHeight={styles.img500} title="Eskimo" skills="ReactJs , Asp.net WebApi core" images={[eskimo1, eskimo2, eskimo3]} ></Work>
            <Work imgHeight={styles.img200} title="Baft-Boom" skills="Wordpress" images={[wp1, wp2, wp3]} ></Work>

        </div>







    </div>;
}

export default MyWorks;