import React from 'react';
import {
    Switch,
    Route,
} from "react-router-dom";
import { Slider, Typography } from '@material-ui/core';
import styles from './styles.module.scss';

const marks = [
    {
        value: 0,
        label: 'beginner',
    },
    {
        value: 35,
        label: 'Intermediate',
    },
    {
        value: 70,
        label: 'Advanced',
    },
];

function valuetext(value) {
    return `${value}Â°C`;
}

function valueLabelFormat(value) {
    return marks.findIndex(mark => mark.value === value) + 1;
}

function Skill(props) {
    const { title, value, description } = props
    return <div className={styles.item}>
        <div className={styles.slider}>
            <Typography variant="h4" gutterBottom>
                {title}
            </Typography>
            <Slider
                classes={{
                    markLabel: styles.markLabel,
                    markLabelActive: styles.markLabel
                }}
                defaultValue={value}
                // valueLabelFormat={valueLabelFormat}
                getAriaValueText={valuetext}
                aria-labelledby="discrete-slider-always"
                step={5}
                marks={marks}
                valueLabelDisplay="on"
            />
        </div>
        {
            description && <div className={styles.description}>
                <div>
                    {description}
                </div>
            </div>
        }
    </div>
}


function CVSkills() {
    return (
        <div className={styles.container}>
            <Skill title="C#" value="85" description="" ></Skill>
            <Skill title="JavaScript" value="80" description="" ></Skill>
            <Skill title="Html" value="75" description="" ></Skill>
            <Skill title="Css" value="70" description="" ></Skill>
            <Skill title="React" value="80" description="" ></Skill>
            <Skill title="Angular" value="70" description="" ></Skill>
            <Skill title="Asp.net Mvc Core" value="80" description="" ></Skill>
            <Skill title="WPF" value="60" description="" ></Skill>
            <Skill title="Wordpress" value="55" description="" ></Skill>
        </div>
    );
}

export default CVSkills;