import React from 'react';
import { Link } from 'react-router-dom';
import styles from './menu.module.scss';
import { withStyles } from '@material-ui/core/styles';
import LanguageIcon from '@material-ui/icons/Language';
import { MenuItem, IconButton, Popper, Grow, Paper, ClickAwayListener, MenuList, Typography } from '@material-ui/core';
import { connectTo } from '../../_utils/generic';


function MyMenu(props) {
    const [open, setOpen] = React.useState(false);
    const anchorRef = React.useRef(null);

    const handleToggle = () => {
        setOpen(prevOpen => !prevOpen);
    };

    const handleClose = event => {
        if (anchorRef.current && anchorRef.current.contains(event.target)) {
            return;
        }

        setOpen(false);
    };

    function handleListKeyDown(event) {
        if (event.key === 'Tab') {
            event.preventDefault();
            setOpen(false);
        }
    }

    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
        if (prevOpen.current === true && open === false) {
            anchorRef.current.focus();
        }

        prevOpen.current = open;
    }, [open]);
    const { location } = props
    return <><ul className={styles.list}>
        <li className={location.pathname === "/" ? styles.active : ""}>
            <Link to="/">Home</Link>
        </li>
        <li className={location.pathname.indexOf("/cv") === 0 ? styles.active : ""}><Link to="/cv">CV</Link></li>
        <li className={location.pathname.indexOf("/myworks") === 0 ? styles.active : ""}><Link to="/myworks">MyWorks</Link></li>
        {/*  <li><Link to="/blog">blog</Link></li> */}
        <li>
            {/* <Typography variant="button">Fa</Typography>
            <IconButton ref={anchorRef} aria-controls={open ? 'menu-list-grow' : undefined} aria-haspopup="true" onClick={handleToggle}>
                <LanguageIcon></LanguageIcon>
            </IconButton>

            <Popper open={open} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
                {({ TransitionProps, placement }) => (
                    <Grow
                        {...TransitionProps}
                        style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
                    >
                        <Paper>
                            <ClickAwayListener onClickAway={handleClose}>
                                <MenuList autoFocusItem={open} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                                    <MenuItem onClick={handleClose}>Fa</MenuItem>
                                    <MenuItem onClick={handleClose}>En</MenuItem>
                                </MenuList>
                            </ClickAwayListener>
                        </Paper>
                    </Grow>
                )}
            </Popper> */}
        </li>
    </ul>

    </>
}


export default withStyles(styles)(connectTo(
    state => ({
        location: state.router.location,
    }),
    {

    }, MyMenu))