import React from 'react';
import {
    Switch,
    Route,
} from "react-router-dom";
import CVAbout from './cvAbout/cvAbout';
import CVExperience from './cvExperience/cvExperience';
import CVMenu from './csMenu/cvMenu';
import styles from './styles.module.scss';
import CVSkills from './cvSkills/cvSkills';

function CV() {
    return (
        <div className={styles.container}>
            <div className={styles.menu}>
                <CVMenu />
            </div>
            <Switch>
                <Route path="/cv/skills">
                    <CVSkills />
                </Route>
                <Route path="/cv/experience">
                    <CVExperience />
                </Route>
                <Route path="/cv">
                    <CVAbout />
                </Route>
            </Switch>
        </div>
    );
}

export default CV;